    titleCache:[],
    titleInfo(page,sendParams,region, okcb, errcb){
        region = ensureRegion(region);
        ApiHttpClient.get(`title.php?lan=${region}&page=${page}`,
        {
            params:sendParams
        }).then(
        (res) => {
            if(sendParams.name === undefined && sendParams.fixstate === undefined && sendParams.type === undefined){
                this.titleCache[page] = res.data;
            }
            okcb(res.data);
        }, 
        errcb);
    },
